<template>
  <div class="dropdown-list" ref="listWrapper">
    <div v-for="(item, index) in list" :key="index" class="list-item" :class="`listItem${index}`"
      @mouseenter="showDropdown(index)" @mouseleave="hideDropdown(index)">
      {{ item.label }}
    </div>
    <div v-if="show" class="dropdown" ref="dropdownRef" :style="{
      width: listWrapperWidth + 'px',
      left: calculateLeftPosition(activeIndex) + 'px'
    }">
      <ul>
        <li v-for="(option, optionIndex) in list[activeIndex]" :key="optionIndex">
          {{ option }}
        </li>
      </ul>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      show: false,
      list: [
        {
          label: '列1',
          options: ['选项1', '选项2', '选项3']
        },
        {
          label: '列2',
          options: ['选项4', '选项5', '选项6']
        },
        {
          label: '列3',
          options: ['选项7', '选项8', '选项9']
        },
        {
          label: '列1',
          options: ['选项1', '选项2', '选项3']
        },
        {
          label: '列2',
          options: ['选项4', '选项5', '选项6']
        },
        {
          label: '列3',
          options: ['选项7', '选项8', '选项9']
        },
        {
          label: '列1',
          options: ['选项1', '选项2', '选项3']
        },
        {
          label: '列2',
          options: ['选项4', '选项5', '选项6']
        },
        {
          label: '列3',
          options: ['选项7', '选项8', '选项9']
        },
        {
          label: '列1',
          options: ['选项1', '选项2', '选项3']
        },
        {
          label: '列2',
          options: ['选项4', '选项5', '选项6']
        },
        {
          label: '列3',
          options: ['选项7', '选项8', '选项9']
        }
      ],
      activeIndex: -1,
      listWrapperWidth: 0
    };
  },
  mounted() {
    this.listWrapperWidth = this.$refs.listWrapper.offsetWidth;
  },
  methods: {
    showDropdown(index) {
      const listItemDom = document.querySelector(`.listItem${index}`)
      this.show = true
      this.activeIndex = index;
      this.$nextTick(() => {
        this.$refs.dropdownRef.style.top = listItemDom.offsetTop + 'px'
      })
    },
    hideDropdown() {
      this.show = false
      this.activeIndex = -1;
    },
    calculateLeftPosition(index) {
      const listWrapper = this.$refs.listWrapper;
      const listItem = listWrapper.children[index];
      const wrapperRect = listWrapper.getBoundingClientRect();
      const itemRect = listItem.getBoundingClientRect();
      let left = itemRect.left - wrapperRect.left;

      // 确保下拉框不会超出父元素右边界
      if (left + this.listWrapperWidth > listWrapper.offsetWidth) {
        left = listWrapper.offsetWidth - this.listWrapperWidth;
      }

      return Math.max(left, 0);
    }
  }
};
</script>

<style scoped>
.horizontal-list {
  position: relative;
  display: flex;
  flex-wrap: wrap;
  width: 500px;
  margin: 50px auto;
  background: #ccc;
}

.list-item {
  position: relative;
  padding: 10px;
  border: 1px solid #ccc;
  cursor: pointer;
}

.dropdown {
  position: absolute;
  top: 100%;
  left: 0;
  right: 0;
  width: 100%;
  background-color: #fff;
  border: 1px solid #ebeef5;
  box-shadow: 0 2px 8px rgba(0, 0, 0, 0.1);
  z-index: 1000;
  border-radius: 4px;
  margin-top: 5px;
}

.dropdown ul {
  list-style: none;
  padding: 0;
  margin: 0;
}

.dropdown li {
  padding: 10px;
}

.dropdown li:hover {
  background-color: #f0f0f0;
}
</style>